
import { mapState, mapMutations } from "vuex";
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";
import { refreshMixin } from "../../common/js/mixin";

export default {
mixins: [refreshMixin],
data() {
return {
paramName: "",
page: {
page: 1,
count: 10
},
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
tab: [
{
type: 0,
value: "最近访问"
},
{
type: 1,
value: "我的客户"
}
],
tabType: 0,
dataList: [],
userID: 0,
clickNum: 0
};
},
mounted() {
let user = JSON.parse(localStorage.getItem("user"));
this.userID = user ? user.data.UserID : 0;
this.onItemClick(0);
},
methods: {
...mapMutations({
setSelectCustomer:"setSelectCustomer"
}),
goBack() {


this.$router.back();
},
async selectCust(item) {
if (this.clickNum > 0) return false;
this.clickNum += 1;
let cache;

try {
cache = localStorage.getItem("client");
} catch (err) {
cache = sessionStorage.getItem("client");
}
let dict = cache ? JSON.parse(cache) : {},
list = cache ? JSON.parse(cache)[this.userID] || [] : [],
index = -1;

list.forEach((data, indexs) => {
if (data.ID === item.ID) {
index = indexs;
}
});
if (index >= 0) {
list.splice(index, 1);

await this.getDataByID(item.ID).then(res => {
item = res;
});
}
list.unshift(item);
list = list.slice(0, 30);

dict[this.userID] = list;
try {
localStorage.setItem("client", JSON.stringify(dict));
} catch (err) {
sessionStorage.setItem("client", JSON.stringify(dict));
}
this.$bus.emit("selectedClient", item);
this.setSelectCustomer(item)
this.refresh("changeOrder", false);
this.refresh("changeReceive", false);
this.$router.back();
},
onItemClick(type) {
this.tabType = type;
if (type === 1) {
this.init(1, 1);
} else {
this.getCacheClient();
}
},
upPull() {
if (this.tabType == 1) {
this.init(2);
} else {
this.$refs.scroll.forceUpdate();
this.$refs.scroll.refresh();
}
},
getDataByID(id) {
return new Promise((resolve, reject) => {
this.getAxios(`/Customer/GetCustomerInfoByID/${id}`)
.then(res => {
resolve(res.Data);
})
.catch(err => {

});
});
},
getCacheClient() {

let cache;
try {
cache = localStorage.getItem("client");
} catch (err) {
cache = sessionStorage.getItem("client");
}
let list = cache ? JSON.parse(cache)[this.userID] || [] : [];
this.$refs.scroll.forceUpdate(true);
this.$refs.scroll.refresh();
this.dataList = [...list];
},
init(type, page) {

this.$vux.loading.show({
text: "加载中"
});
this.page.page = page || this.page.page;
this.getAxios("/Customer/GetPagedCustomerList", {
page: this.page.page,
count: this.page.count,
customerName: this.paramName
})
.then(res => {
this.$vux.loading.hide();
if (res.Data.List.length > 0) {
this.pullUp = {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
};
if (type === 2) {
this.dataList.push(...res.Data.List);
this.$refs.scroll.scroll.finishPullUp();
} else {
this.dataList = res.Data.List;
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate(true);
}
this.page.page += 1;
this.$refs.scroll.refresh();



}else if(type == 1){
this.dataList = [];
this.$refs.scroll.forceUpdate();
} else {
this.$refs.scroll.forceUpdate();
}
})
.catch(err => {
this.$vux.loading.hide();
});
},
goTo(id, name) {
this.$router.push({
path: this.backPath
});
}
},
computed: {
...mapState(["customerID", "customerName"])
},
components: {
XHeader,
Group,
XInput,
Scroll,
Tab,
TabItem,
NoData
}
};



